import React, { useState } from "react";
import styled, { ThemeProvider } from "styled-components";
import Background from "./background.jpg";
import Banner from "./banner.jpg";
import MenuList from "./menu";
import "./App.css";
import UnivSig from "./svg/univsig.svg";
import InnoSig from "./svg/innosig.svg";
import TimeCapsule from "./page/TimeCapsule";

import Guide from "./page/Guide";

import Notice from "./page/Notice";

import Introduce from "./page/Introduce";

const MainPageContainer = styled.div`
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    justify-items: stretch;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: url(${Background}) no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @media screen and (max-width: 425px) {
        grid-template-rows: auto;
    }
`;

const PageContainer = styled.div`
    display: flex;
    justify-items: stretch;
    flex-direction: column;
    width: 100%;
    background-color: #fff;
`;

const PCBlank = styled.div`
    display: block;
    @media screen and (max-width: 425px) {
        display: none;
    }
`;

const PictureBanner = styled.div`
    background: url(${Banner}) no-repeat;
    background-size: cover;
    background-position: 50% 70%;
    height: 350px;
    @media screen and (max-width: 425px) {
        height: 250px;
        background-position: 50% 35%;
    }
`;

const LogoImg = styled.img`
    width: 250px;
    @media screen and (max-width: 425px) {
        width: 40%;
    }
`;

function App() {
    const [tab, setTab] = useState(0);
    const pageChange = () => {
        switch (+tab) {
            case 1:
                return <TimeCapsule />;
            case 2:
                return <Guide />;
            case 3:
                return <Notice />;
            case 4:
                return <Introduce />;
            default:
                return <TimeCapsule />;
        }
    };
    const mainPageChange = () => {
        if (tab === 0) {
            return (
                <MainPageContainer>
                    <PCBlank />
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "flex-start",
                            alignItems: "stretch",
                        }}
                    >
                        <MenuList setTab={setTab} />

                        <div
                            style={{
                                display: "flex",
                                flexDirection: "column",
                                justifyContent: "flex-start",
                                alignItems: "stretch",
                            }}
                        >
                            <div
                                style={{
                                    display: "flex",
                                    flexDirection: "row",
                                    backgroundColor: "#ffffffee",
                                    justifyContent: "center",
                                    alignItems: "center",
                                    flexWrap: "wrap",
                                }}
                            >
                                <LogoImg src={UnivSig} width="250px" alt="서강대학교 로고" />
                                <LogoImg src={InnoSig} width="250px" alt="서강대학교 로고" />
                            </div>
                        </div>
                    </div>
                    <PCBlank />
                </MainPageContainer>
            );
        }
        return (
            <PageContainer>
                <MenuList setTab={setTab} />
                <PictureBanner />
                {pageChange()}
                <br />
                <br />
                <div
                    style={{
                        display: "flex",
                        flexDirection: "row",
                        backgroundColor: "#eee",
                        justifyContent: "center",
                        alignItems: "center",
                        flexWrap: "wrap",
                    }}
                >
                    <LogoImg src={UnivSig} width="250px" alt="서강대학교 로고" />
                    <LogoImg src={InnoSig} width="250px" alt="서강대학교 로고" />
                </div>
            </PageContainer>
        );
    };
    return <ThemeProvider theme={{ isMenuBar: tab !== 0 }}>{mainPageChange()}</ThemeProvider>;
}

export default App;
